import React from 'react'
import { connect } from 'react-redux'
import PhoneApp from "../components/PhoneApp"
import Info from "../components/Info"


let App = ({view}) => (
    <div>
        <div className="iphone-container">
            <img className="iphone" src={require("../static/images/iphone.svg")} />
            <PhoneApp view={view}/>
        </div>
        <Info title="Profile page" text="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum." />
    </div>
)

const mapStateToProps = (state) => {
    return {
        view: state.view
    }
}

App = connect(mapStateToProps)(App)

export default App


